import { DATE_FORMAT_STRING } from "@/config";
import format from "date-fns/format";
import { computed, inject, reactive, ref } from "vue";
import { useStore } from "vuex";
import { required } from "@vuelidate/validators";
import useVuelidate from "@vuelidate/core";
import { buildPdfUrl } from "@/service/utils";

export function useCryoStorageList() {
    const store = useStore();
    const storageList = computed(() => store.state.cryoStorage.list);
    const selectedStorageList = ref();
    const loading = ref(false);

    const getStorageList = () => {
        loading.value = true;
        store
            .dispatch(
                "cryoStorage/getCyroStorageByPatient",
                store.state.artCycle.selectedCycle.patientId
            )
            .finally(() => {
                loading.value = false;
            });
    };

    return { storageList, getStorageList, selectedStorageList, loading };
}

export function useCryoStorageReceivedEmbryos() {
    const store = useStore();
    const dialogVisible = ref(false);
    const selectedEmbryo = ref(null);
    const receivedEmbryos = computed(
        () => store.state.cryoStorage.receivedEmbryos
    );
    const loading = ref(false);
    const isEdit = ref(false);

    const getReceivedEmbryos = () => {
        loading.value = true;
        store
            .dispatch(
                "cryoStorage/getCryoStorageList",
                store.state.artCycle.selectedCycle.cycleId
            )
            .catch(() => {})
            .finally(() => {
                loading.value = false;
            });
    };

    const openPdfReport = (data) => {
        const params = {
            dateFrom: selectedEmbryo.value.freezDate
                ? format(
                      new Date(selectedEmbryo.value.freezDate),
                      DATE_FORMAT_STRING
                  )
                : null,
            dateTo: selectedEmbryo.value.thawDate
                ? format(
                      new Date(selectedEmbryo.value.thawDate),
                      DATE_FORMAT_STRING
                  )
                : null,
            patientId: selectedEmbryo.value.patientId,
            dewar: selectedEmbryo.value.dewar,
            cassColor: selectedEmbryo.value.cassetteColor,
            plugColor: selectedEmbryo.value.plugColor,
            number: null
        };

        let url = buildPdfUrl("CryostorageReport", params);
        window.open(url);
    };

    const cryostorageReportDailog = ref(false);

    return {
        dialogVisible,
        receivedEmbryos,
        selectedEmbryo,
        getReceivedEmbryos,
        loading,
        isEdit,
        openPdfReport,
        cryostorageReportDailog
    };
}

export function useReceivedEgss() {
    const store = useStore();
    const fertilizationLines = computed(
        () => store.state.cryoStorage.fertilizationLines
    );
    const loading = ref(false);

    const getFertilizationLines = () => {
        loading.value = true;
        store
            .dispatch(
                "cryoStorage/getFertilizationLineByCycle",
                store.state.artCycle.selectedCycle.cycleId
            )
            .catch(() => {})
            .finally(() => {
                loading.value = false;
            });
    };

    return {
        fertilizationLines,
        getFertilizationLines,
        loading
    };
}

export function useReceivedEmbryoAddDialog() {
    const store = useStore();
    const dialogVisible = inject("dialogVisible");
    const isEdit = inject("isEdit");
    const selectedEmbryo = inject("selectedEmbryo");
    const loading = ref(false);

    const form = reactive({
        canisterNo: null,
        cassetteType: null,
        cassetteColor: null,
        dewar: null,
        embryoSurvived: null,
        embryoDevelopment: null,
        fertilizationId: null,
        freezDate: null,
        strawId: null,
        plugColor: null,
        source: null,
        thawDate: null,
        cycleId: null,
        patientId: null,
        fate: null,
        day7Stage: null,
        day7Grade: null
    });

    const onShow = () => {
        if (isEdit.value) {
            form.canisterNo = selectedEmbryo.value.canisterNo;
            form.cassetteType = selectedEmbryo.value.cassetteType;
            form.cassetteColor = selectedEmbryo.value.cassetteColor;
            form.dewar = selectedEmbryo.value.dewar;
            form.embryoSurvived = selectedEmbryo.value.embryoSurvived;
            form.embryoDevelopment = selectedEmbryo.value.embryoDevelopment;
            form.fertilizationId = selectedEmbryo.value.fertilizationId;
            form.freezDate = selectedEmbryo.value.freezDate
                ? new Date(selectedEmbryo.value.freezDate)
                : null;
            form.strawId = selectedEmbryo.value.strawId;
            form.plugColor = selectedEmbryo.value.plugColor;
            form.source = selectedEmbryo.value.source;
            form.thawDate = selectedEmbryo.value.thawDate;
            form.cycleId = selectedEmbryo.value.cycleId;
            form.patientId = selectedEmbryo.value.patientId;
            form.fate = selectedEmbryo.value.fate;
            form.day7Stage = selectedEmbryo.value.day7Stage;
            form.day7Grade = selectedEmbryo.value.day7Grade;
        } else {
            form.canisterNo = null;
            form.cassetteType = null;
            form.cassetteColor = null;
            form.dewar = null;
            form.embryoSurvived = null;
            form.embryoDevelopment = null;
            form.fertilizationId = null;
            form.freezDate = null;
            form.strawId = null;
            form.plugColor = null;
            form.source = null;
            form.thawDate = null;
            form.cycleId = null;
            form.patientId = null;
            form.fate = null;
            form.day7Stage = null;
            form.day7Grade = null;
        }
        submitted.value = false;
    };

    const rules = {
        canisterNo: { required },
        cassetteType: { required },
        cassetteColor: { required },
        dewar: { required },
        embryoSurvived: { required },
        embryoDevelopment: { required },
        freezDate: { required },
        strawId: { required },
        plugColor: { required },
        source: { required },
        thawDate: { required },
        fate: { required }
    };
    const v$ = useVuelidate(rules, form);
    const submitted = ref(false);

    const save = () => {
        submitted.value = true;
        if (v$.value.$invalid) return;

        loading.value = true;
        let getFertId = () =>
            isEdit.value ? selectedEmbryo.value.fertilizationId : 0;
        store
            .dispatch("cryoStorage/saveFreezingLine", {
                ...form,
                freezDate: form.freezDate
                    ? format(form.freezDate, DATE_FORMAT_STRING)
                    : null,
                thawDate: form.thawDate
                    ? format(form.thawDate, DATE_FORMAT_STRING)
                    : null,
                cycleId: store.state.artCycle.selectedCycle.cycleId,
                patientId: store.state.artCycle.selectedCycle.patientId,
                fertilizationId: getFertId()
            })
            .then(() => {
                dialogVisible.value = false;
            })
            .catch(() => {})
            .finally(() => {
                loading.value = false;
                submitted.value = false;
            });
    };

    const pugColorOptions = computed(
        () => store.state.cryoStorage.pugColorList
    );
    const cassetteTypeOptions = computed(
        () => store.state.cryoStorage.cassetteTypeList
    );
    const cassetteColorOptions = computed(
        () => store.state.cryoStorage.cassetteColorList
    );

    const fateOptions = reactive([
        "Frozen",
        "Discard",
        "Culture",
        "Donate",
        "Thawed",
        "Transfer",
        "Sent Out"
    ]);

    const dewarOptions = ref([1, 2, 3, 4, 5, 6, 7, 8]);

    return {
        dialogVisible,
        form,
        save,
        loading,
        pugColorOptions,
        cassetteTypeOptions,
        cassetteColorOptions,
        fateOptions,
        dewarOptions,
        onShow,
        v$,
        submitted
    };
}
