<template>
    <h4>Received Embryos</h4>
    <p-data-table
        :value="receivedEmbryos"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm"
        :loading="loading"
        v-model:selection="selectedEmbryo"
        selectionMode="single"
        dataKey="autoFreezId"
    >
        <p-column-group type="header">
            <p-row>
                <p-column header="Source" />
                <p-column header="Freez Date" />
                <p-column header="Straw Id" />
                <p-column header="Stage Grade of each embryo" colspan="8" />
                <p-column header="Dewar" />
                <p-column header="Canister" />
                <p-column header="Plug Color" />
                <p-column header="Cassettle Info" colspan="2" />
                <p-column header="Survive" />
                <p-column header="Develop" />
                <p-column header="Thwing Date" />
            </p-row>
            <p-row>
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="Emb # 1" colspan="2" />
                <p-column header="Emb # 2" colspan="2" />
                <p-column header="Emb # 3" colspan="2" />
                <p-column header="Emb # 4" colspan="2" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
            </p-row>
            <p-row>
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="S" />
                <p-column header="G" />
                <p-column header="S" />
                <p-column header="G" />
                <p-column header="S" />
                <p-column header="G" />
                <p-column header="S" />
                <p-column header="G" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="Color" />
                <p-column header="Type" />
                <p-column header="" />
                <p-column header="" />
                <p-column header="" />
            </p-row>
        </p-column-group>

        <p-column field="source" />
        <p-column field="freezDate">
            <template #body="{data}">
                {{ data.freezDate ? data.freezDate.split(" ")[0] : null }}
            </template>
        </p-column>
        <p-column field="strawId" />
        <p-column field="embryo1s" />
        <p-column field="embryo1g" />
        <p-column field="embryo2s" />
        <p-column field="embryo2g" />
        <p-column field="embryo3s" />
        <p-column field="embryo3g" />
        <p-column field="embryo4s" />
        <p-column field="embryo4g" />
        <p-column field="dewar" />
        <p-column field="canisterNo" />
        <p-column field="plugColor" />
        <p-column field="cassetteColor" />
        <p-column field="cassetteType" />
        <p-column field="embryoSurvived" />
        <p-column field="embryoDevelopment" />
        <p-column field="thawDate">
            <template #body="{data}">
                {{ data.thawDate ? data.thawDate.split(" ")[0] : null }}
            </template>
        </p-column>

        <template #footer>
            <p-button
                label="Add"
                class="p-button-success p-mr-2"
                @click="add"
                v-if="hasCreatePermission"
            />
            <p-button
                label="Edit"
                class="p-button-info p-mr-2"
                :disabled="!selectedEmbryo"
                @click="edit"
                v-if="hasUpdatePermission"
            />
            <p-button
                label="Delete"
                class="p-button-danger p-mr-2"
                v-if="hasDeletePermission"
            />
            <p-button
                label="Report"
                class="p-button-secondary p-mr-2"
                @click="openCryoStorageReportDialog"
            />
        </template>
    </p-data-table>

    <received-embryos-add-dialog />
    <cyro-storage-report-dailog />
</template>

<script>
import { onMounted, provide } from "vue";
import { useCryoStorageReceivedEmbryos } from "@/composable/cyro-storage";

import ReceivedEmbryosAddDialog from "@/components/cryo-storage/received-embryos-add-dialog.vue";
import CyroStorageReportDailog from "@/components/cryo-storage/cyrostorage-report-dialog.vue";
import { usePermissionCheck } from "@/composable/users";

export default {
    components: {
        ReceivedEmbryosAddDialog,
        CyroStorageReportDailog
    },
    setup() {
        const {
            dialogVisible,
            receivedEmbryos,
            selectedEmbryo,
            getReceivedEmbryos,
            loading,
            isEdit,
            openPdfReport,
            cryostorageReportDailog
        } = useCryoStorageReceivedEmbryos();

        const {
            hasUpdatePermission,
            hasCreatePermission,
            hasDeletePermission
        } = usePermissionCheck();

        provide("dialogVisible", dialogVisible);
        provide("selectedEmbryo", selectedEmbryo);
        provide("isEdit", isEdit);
        provide("cryostorageReportDailog", cryostorageReportDailog);
        provide("loading", loading);

        onMounted(() => {
            getReceivedEmbryos();
        });

        return {
            dialogVisible,
            receivedEmbryos,
            selectedEmbryo,
            loading,
            isEdit,
            add: () => {
                dialogVisible.value = true;
                isEdit.value = false;
            },
            edit: () => {
                dialogVisible.value = true;
                isEdit.value = true;
            },
            openPdfReport,
            openCryoStorageReportDialog: () => {
                cryostorageReportDailog.value = true;
            },
            hasUpdatePermission,
            hasCreatePermission,
            hasDeletePermission
        };
    }
};
</script>
