<template>
    <p-dialog
        header="Cryostorage Report"
        v-model:visible="cryostorageReportDailog"
        :style="{ width: '960px' }"
        :modal="true"
        class="p-inputtext-sm p-input-filled"
    >
        <div class="p-formgroup-inline">
            <div class="p-field">
                <label>From</label>
                <p-calendar
                    v-model="form.fromDate"
                    dateFormat="yy-mm-dd"
                    :showIcon="true"
                    :class="{
                        'p-invalid': submitted && v$.fromDate.$invalid
                    }"
                />
                <small
                    v-if="
                        (v$.fromDate.$invalid && submitted) ||
                            v$.fromDate.$pending.$response
                    "
                    class="p-error"
                    >{{ v$.fromDate.required.$message }}
                </small>
            </div>
            <div class="p-field">
                <label>To</label>
                <p-calendar
                    v-model="form.toDate"
                    dateFormat="yy-mm-dd"
                    :showIcon="true"
                    :class="{
                        'p-invalid': submitted && v$.toDate.$invalid
                    }"
                />
                <small
                    v-if="
                        (v$.toDate.$invalid && submitted) ||
                            v$.toDate.$pending.$response
                    "
                    class="p-error"
                    >{{ v$.toDate.required.$message }}
                </small>
            </div>
            <div class="p-field">
                <label>Patient</label>
                <p-auto-complete
                    v-model="patientSearch"
                    :suggestions="fillteredDemographic"
                    @complete="searchDemographic($event)"
                    modelValue="demographicNo"
                    field="label"
                    placeholder="Type to search"
                />
            </div>
            <p-button icon="pi pi-search" class="p-button-sm p-mr-2" />
            <p-button icon="pi pi-times" class="p-button-sm p-button-danger" />
        </div>

        <div class="p-formgroup-inline">
            <div class="p-field">
                <label>Dewar</label>
                <p-dropdown v-model="form.dewar" :options="dewarListOptions" />
            </div>
            <div class="p-field">
                <label>Cassette Color</label>
                <p-dropdown
                    v-model="form.cassetteColor"
                    :options="cassetteColorOptions"
                    optionLabel="label"
                    optionValue="value"
                />
            </div>
            <div class="p-field">
                <label>Plug Color</label>
                <p-dropdown
                    v-model="form.plugColor"
                    :options="pugColorOptions"
                    optionLabel="label"
                    optionValue="value"
                />
            </div>
            <div class="p-field">
                <label>Straw ID</label>
                <p-input-text v-model="form.freezId" />
            </div>
        </div>

        <p-data-table
            :value="report"
            :scrollable="true"
            :loading="loading"
            v-model:selection="selectedReport"
            selectionMode="single"
            dataKey="autoFreezId"
            scrollHeight="400px"
            showGridlines
            class="p-datatable-sm p-mt-1"
            @row-select="fillEmbForm"
        >
            <p-column
                field="freezDate"
                header="Freeze Date"
                style="max-width: 100px"
            />
            <p-column
                field="strawId"
                header="Straw ID"
                style="max-width: 80px"
            />
            <p-column
                field="cycleId"
                header="Cycle ID"
                style="max-width: 80px"
            />
            <p-column header="Patient" style="width: 250px;max-width: 250px">
                <template #body="{data}">
                    {{ data.patientId }} - {{ data.firstName }}
                    {{ data.lastName }}
                </template>
            </p-column>
            <p-column field="dewar" header="Dewar" style="max-width: 80px" />
            <p-column
                field="canisterNo"
                header="Canister"
                style="max-width: 80px"
            />
            <p-column
                field="plugColor"
                header="Plug Color"
                style="max-width: 80px"
            />
            <p-column
                field="cassetteColor"
                header="Cass Color"
                style="max-width: 80px"
            />
            <p-column field="cassetteType" header="Cass Type" />
        </p-data-table>

        <div class="p-formgroup-inline p-mt-2">
            <div class="p-field">
                <label>Emb 1G</label>
                <p-input-text v-model="emb._1g" disabled />
            </div>
            <div class="p-field">
                <label>Emb 1S</label>
                <p-input-text v-model="emb._1s" disabled />
            </div>
            <div class="p-field">
                <label>Emb 2G</label>
                <p-input-text v-model="emb._2g" disabled />
            </div>
            <div class="p-field">
                <label>Emb 2S</label>
                <p-input-text v-model="emb._2g" disabled />
            </div>
        </div>

        <div class="p-formgroup-inline">
            <div class="p-field">
                <label>Emb 3G</label>
                <p-input-text v-model="emb._3s" disabled />
            </div>
            <div class="p-field">
                <label>Emb 3S</label>
                <p-input-text v-model="emb._3g" disabled />
            </div>
            <div class="p-field">
                <label>Emb 4G</label>
                <p-input-text v-model="emb._4s" disabled />
            </div>
            <div class="p-field">
                <label>Emb 4S</label>
                <p-input-text v-model="emb._4s" disabled />
            </div>
        </div>

        <div class="p-grid p-mt-2">
            <p-button label="Report" @click="getReport" class="p-mr-2" />
            <p-button label="Close" @click="close" />
        </div>
    </p-dialog>
</template>

<script>
import { useCryostorageReportDialog } from "@/composable/art-cycle";
import { usePatientSearch } from "@/composable/art-cycle";
import { onMounted } from "vue-demi";
export default {
    setup() {
        const {
            store,
            report,
            loading,
            cryostorageReportDailog,
            pugColorOptions,
            cassetteColorOptions,
            dewarListOptions,
            form,
            v$,
            submitted,
            getReport,
            patientSearch,
            selectedReport,
            fillEmbForm,
            emb
        } = useCryostorageReportDialog();

        const { fillteredDemographic, searchDemographic } = usePatientSearch();

        onMounted(() => {
            store.dispatch("cryoStorage/getCryoStorageOptions");
        });

        return {
            cryostorageReportDailog,
            report,
            loading,
            pugColorOptions,
            cassetteColorOptions,
            dewarListOptions,
            form,
            v$,
            submitted,
            getReport,
            fillteredDemographic,
            searchDemographic,
            patientSearch,
            selectedReport,
            fillEmbForm,
            emb,
            close: () => {
                cryostorageReportDailog.value = false;
            }
        };
    }
};
</script>

<style scoped>
.p-calendar {
    height: 24.5px;
}
:deep() .p-datatable-wrapper {
    height: 400px;
}
</style>
