<template>
    <p-fieldset legend="Cryo Storage">
        <cryo-storage-received-table />
        <cryo-storage-received-embryos />
        <cryo-storage-received-eggs />
    </p-fieldset>

    <p-fieldset legend="Embryo Cryo Storage" id="semen-cryo-storage">
        <semen-cryo-storage-form />

        <p-data-table
            :value="table"
            responsiveLayout="scroll"
            showGridlines
            class="p-datatable-sm p-mt-1 p-mb-2"
        >
            <p-column header="Freez Date" field="freezeDate" />
            <p-column header="vividId" field="vividId" />
            <p-column header="Dewar" field="dewar" />
            <p-column header="Canister" field="canister" />
            <p-column header="Plug Color" field="plugColor" />
            <p-column header="Thawing Date" field="thawingDate" />
            <p-column header="Tech ID" field="techId" />
        </p-data-table>

        <p-button
            label="Edit"
            class="p-button-info p-mr-2"
            v-if="hasUpdatePermission"
        />
        <p-button
            label="Delete"
            class="p-button-danger"
            v-if="hasDeletePermission"
        />
    </p-fieldset>
</template>

<script>
import { onMounted, ref } from "vue";
import { useSemenCryoStorageInit } from "@/composable/semen-cryo-storage";
import { useStore } from "vuex";

import CryoStorageReceivedTable from "@/components/cryo-storage/table.vue";
import CryoStorageReceivedEmbryos from "@/components/cryo-storage/received-embryos.vue";
import CryoStorageReceivedEggs from "@/components/cryo-storage/received-eggs.vue";
import SemenCryoStorageForm from "@/components/semen-cryo-storage/form.vue";
import { usePermissionCheck } from "@/composable/users";

export default {
    components: {
        CryoStorageReceivedEmbryos,
        CryoStorageReceivedEggs,
        CryoStorageReceivedTable,
        SemenCryoStorageForm
    },
    setup() {
        const table = ref([]);
        const store = useStore();
        const { getProvivers, getSemenCryoStorage } = useSemenCryoStorageInit();

        const {
            hasUpdatePermission,
            hasDeletePermission
        } = usePermissionCheck();

        onMounted(() => {
            store.dispatch("cryoStorage/getCryoStorageOptions");
            getProvivers();
            getSemenCryoStorage();
        });

        return {
            table,
            hasUpdatePermission,
            hasDeletePermission
        };
    }
};
</script>
